<!-- 蓝色登录页面2 -->
<template>
	<view class="bg-secondary" style="height:100vh;">
		<view class="login-title">
			<view class="login-title__text" @click="goLogin3()">
				您好，欢迎使用轻松考
			</view>
		</view>
		<view class="login-view">
			<view class="login-view-wrap fz28">
				<form class="login-view-form">
					<view class="login-view-form__item">
						<text class="login-view-form__text">手机号</text>
						<input class="login-view-form__inp" type="number" name="phone" placeholder="请输入您的手机号" maxlength="11" v-model="loginData.mobile" />
					</view>
					<view class="login-view-form__item">
						<text class="login-view-form__text">密码</text>
						<input class="login-view-form__inp" type="password" name="code" maxlength="18" placeholder="请输入您的密码" v-model="loginData.password" />
					</view>
					<button class="login-view-form__login button-direction mb20" @tap="handleLogin()">登 录</button>
					<view class="login-view-form__reg button-direction" @tap="reg()">注 册</view>
				</form>
			</view>
		</view>
	</view>
</template>
<script>
import store from '@/store'
import * as Validate from '@/utils/validate'
export default {
	data() {
		return {
            // 正在加载
            isLoading: false,
            loginData: {
                mobile: '13111182031', //手机号码
			    password: 'kaikaiqq123' //密码
            }
		};
	},
	onLoad() {},
	methods: {
		// 跳转第二个登陆模版
		goLogin3(){
			uni.navigateTo({
				url: 'index3'
			});
		},
        /**
         * 验证手机号
         */
        validteMobile(str) {
            if (Validate.isEmpty(str)) {
                this.$toast('请先输入手机号')
                return false
            }
            console.log(Validate.isMobile(str))
            if (!Validate.isMobile(str)) {
                this.$toast('请输入正确格式的手机号')
                return false
            }
            return true;
        },
        /**
         * 验证密码
         */
        validtePassword(str) {
            if (Validate.isEmpty(str)) {
                this.$toast('请先输入密码')
                return false
            }
            return true;
        },
        /**
         * 表单验证
         */
        formValidation() {
            if (!this.validteMobile(this.loginData.mobile) || !this.validtePassword(this.loginData.password)) {
                return false;
            }
            return true;
        },
		/**
         * 点击登录
         */
		handleLogin() {
            if (!this.isLoading && this.formValidation()) {
                this.submitLogin()
            }
		},
        /**
         * 确认登陆
         */
        submitLogin() {
            this.isLoading = true
            store.dispatch('users/login', this.loginData).then(res => {
                this.$toast('登录成功')
                // 跳转回首页
                setTimeout(() => {
                    this.onNavigateBack(1)
                }, 2000)
            })
            .finally(() => this.isLoading = false)
        },
        /**
         * 跳转回首页
         */
        onNavigateBack(delta = 1) {
            const pages = getCurrentPages()
            if (pages.length > 1) {
                uni.navigateBack({
                    delta: Number(delta || 1)
                })
            } else {
                this.$navTo('pages/index/tabBar')
            }
            
        },
		//注册按钮点击
		reg() {
			uni.showToast({ title: '注册跳转', icon: 'none' });
        }
	}
};
</script>
